.overlay{
    width: 100%;
    height: 100vh;
    background: url('../tracks/bg2.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}
.card{
    background: linear-gradient(rgba(27, 119, 172, 0.452),rgba(2, 13, 24, 0.897)) !important;
    box-shadow:  15px -8px 99px 38px rgba(29, 134, 153, 0.37);
    border: none !important;
}
.card-header{
    color: #c5c5c5 !important;
}
.card-footer button.btn.btn-primary.btn-block{
    background: #05AFFD !important;
}
.btn-custom {
    background-color: DodgerBlue;
    border: none;
    color: white;
    padding: 12px 30px;
    cursor: pointer;
    font-size: 20px;
  }
  
  /* Darker background on mouse-over */
  .btn-custom:hover {
    background-color: RoyalBlue;
  }

  nav{
      height: 100px;
      background: none;
      padding: 30px 16px;
      margin-bottom: 2rem;
  }

  nav ul.nav-custom{
      display: flex;
      flex-direction: row;
      list-style: none;
      overflow: hidden;
      justify-content: space-evenly;
      color: #c5c5c5;
  }
  nav ul.nav-custom li.nav-custom-brand{
      font-size: 1.4rem;
      margin-top: 8px;
  }
  nav ul.nav-custom li.nav-custom-item{
    margin: 10px;
    width: 20%;
  }