.wrap{
    display: flex;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.wrap::before{
    content: '';
    position: absolute;
    height: 100vh;
    width: 100%;
    background: url('./bg2.jpg');
    background-repeat: no-repeat;
    background-size: cover;
}
.wrap .card{
    background: linear-gradient(rgba(22, 22, 22, 0.863),rgba(14, 13, 13, 0.863)) !important;
    box-shadow:  15px -8px 99px 38px rgba(29, 134, 153, 0.37);
}
.input-group-text,.custom-file-label{
    background: rgba(34, 34, 34, 0.808) !important;
    color: #858484fb !important;
    border-color: rgba(34, 34, 34, 0.808) !important;
}
.card-header{
    color: #858484fb !important;
}
.form-control,.custom-file-input{
    background: rgba(34, 34, 34, 0.808) !important;
    border-color:  rgba(34, 34, 34, 0.808) !important;
    color: #858484fb !important;
}
.btn-primary{
    background: #05AFFD !important;
}